import React, { useEffect, useRef } from 'react';
import emailjs from '@emailjs/browser';
import { start } from 'repl';

type ContactProps = {
	language: boolean;
};

const Contact :React.FC<ContactProps> = (props)=>{
	useEffect(()=>{
		console.log('Contact rerendering')
	},[])
	const form = useRef<HTMLFormElement>(null);

	const sendEmail = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
	
		if (form.current) {
			emailjs.sendForm('service_xufsiqw', 'template_v781l0x', form.current, 'fQi_3WNvdw5HkfvUS')
			.then((result) => {
				//console.log(result.text);
				alert(`${props.language ? "You sent the Email successfully!" : "  !"}`)
				// window.location.href = '/';
				window.location.reload();
			})
			.catch((error) => {
				console.log(error.text);
			});
		}
	};
	return(
		<div className="container">
			<h1>Contact</h1>
			<section className="contact">
				<article>
					<p className="desc">
						{ 
							props.language 
							? 'Get in touch or shoot me an email directly on codeveloper23@gmail.com'
							: 'codeveloper23@gmail.com ,   '
						}
					</p>
					<form ref={form} onSubmit={sendEmail}>
						<div className="row">
							<label>
								{ 
									props.language 
									? 'Name'
									: ''
								}
							</label>
							<input type="text" name="user_name" required/>
						</div>
						<div className="row">
							<label>
								{ 
									props.language 
									? 'Email'
									: ''
								}
							</label>
							<input type="email" name="user_email" required/>
						</div>
						<div className="row">
							<label>
								{ 
									props.language 
									? 'Message'
									: ''
								}
							</label>
							<textarea name="message" required/>
						</div>
						<div className="row submit">
							<input type="submit" className="hov-btn" value={props.language ? "Send" : ""} />
						</div>
					</form>
				</article>
			</section>
		</div>
	)
}

export default Contact;