import { useEffect } from 'react';
import projectList from './projectList'

type LanguageSwitchProps = {
	language: boolean,
	setLanguage: React.Dispatch<React.SetStateAction<boolean>>
};

const LanguageSwitch :React.FC<LanguageSwitchProps> = (props)=>{
	useEffect(()=>{
		console.log('LanguageSwitch rerendering')
	},[])
	return(
		<aside className="switch-wrap">
			<span>KR</span>
			<button className={`switch ${props.language ? 'on' : ''}`} onClick={()=>{
				props.setLanguage(!props.language)
			}}>
				<span className="circle"></span>
			</button>
			<span>EN</span>
		</aside>
	)
}



export default LanguageSwitch;