import { useEffect } from "react";
import LanguageSwitch from "./LanguageSwitch";
import { NavLink } from 'react-router-dom';

type IntroProps = {
	language: boolean
};

const Intro :React.FC<IntroProps> = (props)=>{
	useEffect(()=>{
		console.log('Intro rerendering')
	},[])
	return(
		<div className="container">
			<section className="intro">
				<div className="title">
					<div className="introTitle">
						<b className="first">HI,</b><b className="last">&nbsp;I'M <span className="stack" title="SONNY!"><span>SONNY!</span></span></b>
					</div>
				</div>
				<p className="desc">
					{
						props.language
						? 
						<>
							Your friendly neighborhood frontend developer. <br />
							A Result-Oriented Web Developer building and managing Websites and Web Applications that leads to the success of the overall product
						</>
						: 
						<>
							 <br />
							                
						</>
					}
				</p>
				<NavLink to="/project" className="btn">
					<span>View My Project</span>
					<img src={`${process.env.PUBLIC_URL}/img/btn-next.svg`} alt="" />
				</NavLink>
			</section>
		</div>
	)
}

export default Intro;