import { useEffect, useRef, useState } from 'react';
import projectList from './projectList'
import Modal from './Modal'

interface ProjectProps {
    language: boolean;
    setLanguage: React.Dispatch<React.SetStateAction<boolean>>;
}

const Project: React.FC<ProjectProps> = (props) => {
	useEffect(()=>{
		console.log('Project rerendering')
	},[])
	const imageUrls :string[] = []; //   URL  array

	projectList.forEach((el, i)=>{
		imageUrls.push(`${process.env.PUBLIC_URL}/img/jpg/${el.rsc.name}_${el.responsive.main}.jpg`)
	})

	let [isLoading, setIsLoading] = useState(true); //       
	let [modal, setModal] = useState(false);
	let [target, setTarget] = useState(0);
  
	useEffect(() => {
		const imageLoadStatus = Array(imageUrls.length).fill(true); //       
		let loadedImageCount = 0; //     
	
		const checkAllImagesLoaded = () => {
			//    
			if (loadedImageCount === imageUrls.length) {
				setIsLoading(false);
			}
		};
	
		const handleImageLoad = (index:number) => {
			//      
			imageLoadStatus[index] = true; //     true 
			loadedImageCount++; //    
			checkAllImagesLoaded(); //    
		};
	
		//      onload   
		imageUrls.forEach((imageUrl, index) => {
			const image = new Image();
			image.onload = () => handleImageLoad(index);
			image.src = imageUrl;
		});
	
		//      
		return () => {
			//   onload   
			imageUrls.forEach((imageUrl, index) => {
			const image = new Image();
			image.onload = null;
			});
		};
	}, [imageUrls]); // imageUrls    useEffect 
	return(
		<div className="container">
			{ isLoading && <aside className="msg-cover loading"><img src={`${process.env.PUBLIC_URL}/img/icon-loading.gif`} alt="" /></aside> }
			{
				modal
				?<Modal modal={modal} setModal={setModal} target={target} setTarget={setTarget} language={props.language} setLanguage={props.setLanguage}/>
				:null
			}
			<h1>Project</h1>
			<section>
				<ul className="pj">
					{
						projectList.map((el, i)=>{
							return(
								<li key={i}>
									<a href="#" onClick={(e)=>{
										e.preventDefault();
										setModal(!modal)
										setTarget(i)
									}}>
										<img src={`${process.env.PUBLIC_URL}/img/webp/${el.rsc.name}_${el.responsive.main}.webp`} alt=""/>
										<div className="cover"></div>
										<div className="desc">
											<p className="pro-tit">
												{
													props.language
													? el.projectName.eng
													: el.projectName.kor
												}
											</p>
											<div className="tags">
												{
													el.skills.map((element, j)=>{
														return(
															<span className="tag" key={j}>{element}</span>
														)
													})
												}
											</div>
										</div>
									</a>
								</li>
							)
						})
					}
				</ul>
			</section>
		</div>
	)
}

export default Project;