import React, { Suspense, lazy, useEffect, useRef, useState } from 'react';
import './css/style.css';
import { Routes, Route } from 'react-router-dom';
import Navigate from './components/Navigate'
import Intro from './components/Intro'
import Project from './components/Project'
import About from './components/About'
import Contact from './components/Contact'
import LanguageSwitch from './components/LanguageSwitch'
import projectList from './components/projectList'
// import Test from './components/Test'

const Test = lazy(()=> import('./components/Test'));

const App: React.FC = () => {
	let [language, setLanguage] = useState(true);

	return (
		<div className="wrap">
			<LanguageSwitch language={language} setLanguage={setLanguage}/>
			<header>
				<Navigate></Navigate>
			</header>
			<main>
				<Routes>
					<Route path="/" element={
						<Intro language={language}></Intro>
					}></Route>
					<Route path="/about" element={
						<About language={language}/>
					}></Route>
					<Route path="/project" element={
						<Project language={language} setLanguage={setLanguage}></Project>
					}></Route>
					<Route path="/contact" element={
						<Contact language={language} />
					}></Route>
					<Route path="/test" element={
						<Suspense fallback={<div><img src={`${process.env.PUBLIC_URL}/img/icon-loading.gif`} alt="" /></div>}>
							<Test language={language} setLanguage={setLanguage}></Test>
						</Suspense>
					}></Route>
					<Route path="*" element={
						<aside className="msg-cover none">404 This page could not be found.</aside>
					}></Route>
				</Routes>
			</main>
			<footer>
				<div className="container">
					<p>Powered by React.js and TypeScript</p>
				</div>
			</footer>
		</div>
	);
}

export default App;