import React, { useEffect, useRef, useState} from 'react';
import projectList from './projectList';
import LanguageSwitch from './LanguageSwitch'


type ModalProps = {
	language: boolean;
	setLanguage: React.Dispatch<React.SetStateAction<boolean>>;
    modal: boolean;
	setModal: React.Dispatch<React.SetStateAction<boolean>>;
	target: number;
	setTarget: React.Dispatch<React.SetStateAction<number>>;
};

const Modal: React.FC<ModalProps> = (props) => {
	useEffect(()=>{
		console.log('Modal rerendering')
	},[])


	let [aniToggle, setAniToggle] = useState(false);
	let [duration, setDuration] = useState(0);

	useEffect(()=>{
		let width :number = projectList[props.target].rsc.width;
		let height :number = projectList[props.target].rsc.height;	
		let per :number = 1366 / width;
		let dc :number = Math.round(per * height / 373);
		setDuration(dc);
		console.log(height)
	
		let dt = setTimeout(()=>{
			setAniToggle(true)
		},100)
		
		return()=>{
			clearTimeout(dt)
			setAniToggle(false)
		}
	}, [props.target])
	  
	useEffect(()=>{
        const bodyStyle = document.body.style;
        bodyStyle.overflow = 'hidden';

        return () => {
            bodyStyle.overflow = 'auto scroll';
        };
    }, []); 

	const scrollRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (scrollRef.current) {
            scrollRef.current.scrollTop = 0;
        }
    }, [props.target]);

    return (
		<aside className="modal-cover" onClick={(e)=>{
			props.setModal(!props.modal)
		}}>
			<div className="modal-wrap" onClick={(e)=>{e.stopPropagation()}}>
				<div className="modal" ref={scrollRef}>
					<button className="close" onClick={()=>{
						props.setModal(!props.modal)
					}}>
						<img src={`${process.env.PUBLIC_URL}/img/btn-back.svg`} alt="" />
					</button>
					<h2>
						{
							props.language
							? projectList[props.target].projectName.eng
							: projectList[props.target].projectName.kor
						}
					</h2>
					<div className={`img-wrap ${ aniToggle ? 'run' : ''}`} style={{
						backgroundImage : `url(${process.env.PUBLIC_URL}/img/webp/${projectList[props.target].rsc.name}_${projectList[props.target].responsive.main}.webp)`,
						animationDuration : `${duration}s`,
						width : `${projectList[props.target].responsive.main == 'pc' ? '100%' : 'min(100%, 300px)'}`
					}}>
					</div>
					<dl>
						<dt>
							<span>About</span>
						</dt>
						<dd>
							{
								props.language
								? projectList[props.target].description.eng
								: projectList[props.target].description.kor
							}
						</dd>
						<dt>Technologies</dt>
						<dd>
							<div className="tags">
								{
									projectList[props.target].skills.map((el, j)=>{
										return(
											<span className="tag" key={j}>{el}</span>
										)
									})
								}
							</div>
						</dd>
						<dt>Position</dt>
						<dd>
							{
								projectList[props.target].position
							}
						</dd>
						{
							projectList[props.target].url
							&&
							<>
								<dt>Link</dt>
								<dd><a href={projectList[props.target].url} target="_blank" className="visit hov-btn">Visit Website</a></dd>
							</>
						}
					</dl>
					<div className="bar">
						<button onClick={()=>{
							if(props.target == 0){
								props.setTarget(projectList.length-1)
							} else {
								props.setTarget(props.target-1)
							}
						}}>
							<img src={`${process.env.PUBLIC_URL}/img/btn-previous.svg`} alt="" />
							<span>Previous</span>
						</button>
						<button onClick={()=>{
							if(props.target == projectList.length-1){
								props.setTarget(0)
							} else {
								props.setTarget(props.target+1)
							}
						}}>
							<span>Next</span>
							<img src={`${process.env.PUBLIC_URL}/img/btn-next.svg`} alt="" />
						</button>
					</div>
				</div>
			</div>
		</aside>
    );
};

export default Modal;